export default {
	data () {
		return {
			RichText: []
		}
	},
	onLoad() {
		this.initRichText()
	},
	methods: {
		//到投诉
		goToComplaint () {
			uni.navigateTo({
				url: '../complaint/complaint'
			})
		},
		goToRichText (id) {
			uni.navigateTo({
				url: '../rich-text/rich-text?id=' + id
			})
		},
		async initRichText () {
			let res = await this.$fetch(this.$api.articleType,{type: 14},"Post","Form")
			console.log(res)
			if (res.code == 0) {
				this.RichText = res.data
			}
		},
	}
}
